@charset "UTF-8";

body {
  text-align: center;
  margin: 0;
}/* HEADER */.header {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  z-index: 2;
}

.header_name {
  width: 100vw;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #099d8d;
  opacity: 0.95;
  color: white;
}

.header_navbar {
  width: 100vw;
  background-color: #14d9c4;
  opacity: 0.9;
  color: white;
}

.header_navbar-list {
  display: flex;
  justify-content: space-between;
}

.header_navbar_item {
  list-style: none;
}

a {
  color: inherit;
  font-weight: bolder;
  text-decoration: none;
}

.main_hero {
  display: flex;
  align-items: flex-end;
  position: relative;
}

.main {
  margin: 0 auto;
}

.main_hero_img {
  width: 100%;
  height: 421px;
  background-image: url("../../../assets/images/grupal.jpg");
  background-size: cover;
  background-position: center 60px;
  position: relative;
}

.main_hero_claim {
  padding: 30px 10px 30px 10px;
  position: absolute;
  z-index: 1;
  transform: scale(0.9);
  margin: 0 auto;
  background-color: white;
  font-size: 24px;
}

aside {
  background-color: rgba(184, 184, 185, 0.2);
  text-align: center;
  padding: 30px 15px;
  margin-top: -20px;
}

h2,h3 {
  font-family: "Rubik", sans-serif;
}

.main_team_title {
  color: #14d9c4;
  text-transform: uppercase;
}

.main_team_sw_title {
  color: #099d8d;
  font-size: 22px;
}

p {
  font-family: "Open Sans", sans-serif;
  line-height: 24px;
  text-align: center;
  color: #54585a;
}

.main_team {
  text-align: center;
  margin-top: 25%;
}

.main_team_description {
  padding: 0 15px;
}

.main_team_triangle {
  position: relative;
  z-index: 1;
  margin: 0 auto;
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 30px solid white;
}

.main_who {
  color: #14d9c4;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.who_list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.main_who_person {
  display: flex;
  flex-direction: column;
  align-content: space-between;
}

.main_who_photo_container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  margin: 55px;
}

.main_who_photo {
  border-radius: 4px;
  width: 100%;
  border: solid rgba(0, 0, 0, 0.08) 5px;
}

.main_who_text {
  display: flex;
  margin: 0 15px;
  flex-direction: column;
  text-align: center;
  font-size: 16px;
  line-height: 24px;
  color: #54585a;
  font-family: open sans;
}

.main_who_name_container,.main_who_description,.main_who_social {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  color: #099d8d;
}

.main_who_description {
  margin: 20px 0;
}

.main_who_social {
  margin: 0 0 50px 0;
}

.main_who_name,.main_who_description_text {
  display: inline;
  margin: 0;
}

.main_who_social_list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.main_who_social_item:not(:first-child)::before {
  content: "Â·";
  vertical-align: middle;
  font-size: 20px;
  margin: 0 11px;
}

.fa-twitter,.fa-github,.fa-linkedin,.fa-envelope {
  color: #099d8d;
  font-size: 20px;
}

.footer {
  margin: 0 auto;
  border-top: 1px solid #b8b8b9;
}

.footer_reg {
  padding-top: 30px;
}

.footer_reg p {
  color: #b8b8b9;
}

.footer_navbar {
  padding-top: 30px;
  color: grey;
}

.footer_logo {
  width: 200px;
  margin: 0 auto;
  margin: 10% 0 8% 0;
