@media (min-width: 320px) and (max-width: 767px) {
  .header_navbar_name {
    display: none;
  }

  .main_hero_claim {
    margin-top: -10%;
  }

  .header_navbar_name {
    display: none;
  }
}

@media all and (min-width: 768px) {
  .header_name {
    display: none;
  }

