    background-color: #099d8d;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 40%;
  }

  .header_navbar_item {
    padding-left: 1%;
    padding-right: 1%;
  }

  .header_navbar_name {
    font-weight: bold;
    justify-content: flex-start;
  }

  .main_hero_img {
    background-position: center -80px;
  }

  .main_hero_claim {
    opacity: 0.8;
    position: absolute;
  }

  aside {
    padding: 45px;
    display: flex;
  }

  .main_team_title {
    color: #099d8d;
  }

  .main_team_skills {
