  }

  .main_who_person {
    flex-direction: row;
    align-items: flex-start;
    padding: 25px 50px;
  }

  .main_who_person:nth-child(odd) {
    flex-direction: row-reverse;
  }

  .main_who_photo_container {
    margin: 0;
  }

