    box-sizing: border-box;
    width: 220px;
  }

  .main_who_text {
    text-align: right;
    margin: 10px 60px 0 0;
  }

  .main_who_text_odd {
    text-align: left;
    margin: 10px 0 0 60px;
  }

  .main_who_social_list {
    justify-content: flex-end;
  }

  .main_who_social_list_odd {
    justify-content: flex-start;
